$underlineColor: #00B388;
$underlineHeight: 5%;

body {
    font-size: 17px;
}

a {
    @apply .font-semibold;
    @apply .no-underline;
    @apply .text-blue-600;

    &:hover {
        @apply .text-blue-800;
    }
    cursor: pointer;
}

.link-1 {
    position: relative;
    text-decoration: none;
    display: inline-block;
    color: white;
    padding: 0 1px;
    transition: color ease 0.3s;

    &::after {
        content: '';
        position: absolute;
        z-index: -1;
        width: 100%;
        height: $underlineHeight;
        left: 0;
        bottom: 0;
        background-color: $underlineColor;
        transition: all ease 0.3s;
    }

    &:hover {
        color: white;

        &::after {
            height: 100%;
        }
    }
}

.theme-light {
    @apply .bg-gray-100;
    @apply .text-gray-800;
}

.dark {
    background-color: #272727;
    @apply .text-white;
}

.blink_me {
    animation: blinker 1s linear infinite;
}

@keyframes blinker {
    50% { opacity: 0; }
}

figure {
    filter: grayscale(.8);
}

figure img{
    width: 100%;
    height: 100%;
}
figure::before {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 2;
    display: block;
    content: '';
    width: 0;
    height: 0;
    background: rgba(0,0,0,.2);
    border-radius: 100%;
    transform: translate(-50%, -50%);
    opacity: 0;
}
figure:hover{
    animation: bloom ease-in-out .75s forwards;
}
figure:hover::before {
    animation: circle .75s;
}
@keyframes bloom {
    0% {
        filter: grayscale(.8);
    }
    40% {
        filter: grayscale(.5);
    }
    100% {
        filter: grayscale(0);
    }
}
@keyframes circle {
    0% {
        opacity: .5;
        background: rgba(213,156,34,.2);

    }
    40% {
        opacity: 1;
        background: rgba(213,34,160,.2);
    }
    100% {
        width: 200%;
        height: 200%;
        opacity: 0;
    }
}

blockquote {
    @apply .border-blue-400;
    @apply .border-l-4;
    @apply .font-normal;
    @apply .italic;
    @apply .my-8;
    @apply .pl-6;
    @apply .text-gray-800;
    @apply .text-lg;
}

code {
    @apply .bg-gray-300;
    @apply .px-2;
    @apply .py-px;
    @apply .rounded;
    @apply .text-sm;
}

code.hljs {
    @apply .bg-transparent;
    @apply .p-0;

    .hljs-comment,
    .hljs-keyword,
    .hljs-meta {
        @apply .font-normal;
        @apply .not-italic;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    @apply .leading-tight;
    @apply .mb-4;
    @apply .mt-8;
    @apply .text-gray-900;

    &:first-child {
        @apply .mt-0;
    }
}

h1 {
    @apply .font-extrabold;
    @apply .text-5xl;
}

h2 {
    @apply .font-bold;
    @apply .text-4xl;
}

h3 {
    @apply .font-bold;
    @apply .text-3xl;
}

h4 {
    @apply .font-normal;
    @apply .text-2xl;
}

h5 {
    @apply .font-normal;
    @apply .text-xl;
}

h6 {
    @apply .font-light;
    @apply .text-lg;
}

hr {
    @apply .border-b;
    @apply .border-blue-200;
    @apply .my-12;
    @apply .rounded-full;
}

li {
    ul,
    ol {
        @apply .my-0;
        @apply .ml-8;
    }
}

ol,
ul {
    @apply .my-4;
}

ol {
    @apply .list-decimal;
}

ul {
    @apply .list-disc;
}

p {
    @apply .my-3;

    @screen md {
        @apply .my-6;
    }
}

pre {
    @apply .bg-gray-200;
    @apply .leading-loose;
    @apply .my-6;
    @apply .overflow-x-auto;
    @apply .p-4;
    @apply .rounded;
    @apply .shadow;
    @apply .text-base;

    code {
        @apply .bg-transparent;
        @apply .block;
        @apply .p-0;
    }
}

::selection {
    @apply .bg-blue-500;
    @apply .text-white;
}
